<template>
  <div ref="map" class="lodge-map">
    <canvas id="pixi" :width="getMapSize().width" :height="getMapSize().height"></canvas>
    <!-- <img class="image" src="@/assets/images/map.png" /> -->
  </div>
</template>

<script setup>
import { ref, reactive, computed, onMounted } from 'vue';
import * as Pixi from 'pixi.js';
import PixiInputField from '@/plugins/pixi/InputField.js';
import { Viewport } from '@/plugins/pixi/pixi-viewport';

const WORLD_WIDTH = 5000;
const WORLD_HEIGHT = 3500;

const map = ref();

const controls = ref('idle');
const destination = ref('');
const origin = ref('');
const navi = ref([]);

function getMapSize() {
  const width = map.value?.offsetWidth;
  const height = map.value?.offsetHeight;

  return { width: width ?? 0, height: height ?? 0 };
}

function degToRad(deg) {
  return deg * (Math.PI / 180);
}

function drawDashedLine(graphics, x, y, x2, y2, draw = true, dashArray) {
  if (!dashArray) {
    dashArray = [16, 8];
  }

  if (!draw) {
    const tmp = [];
    for (let i = 0; i < dashArray.length; i += 2) {
      tmp.push(dashArray[i + 1]);
      tmp.push(dashArray[i]);
    }
    dashArray = tmp;
  }

  const dashCount = dashArray.length;

  graphics.moveTo(x, y);

  const dx = (x2 - x);
  const dy = (y2 - y);
  const slope = dy / dx;
  let distRemaining = Math.sqrt(dx * dx + dy * dy);
  let dashIndex = 0;
  while (distRemaining >= 0.1) {
    let dashLength = dashArray[dashIndex++ % dashCount];
    if (dashLength > distRemaining) {
      dashLength = distRemaining;
    }

    // eslint-disable-next-line no-mixed-operators
    const xStep = Math.sqrt(dashLength * dashLength / (1 + slope * slope));
    x += xStep;
    y += slope * xStep;
    graphics[draw ? 'lineTo' : 'moveTo'](x, y);
    distRemaining -= dashLength;
    draw = !draw;
  }
}

async function createMap(viewport) {
  const offsetX = WORLD_WIDTH / 2;
  const offsetY = WORLD_HEIGHT / 2;

  // eslint-disable-next-line global-require, @typescript-eslint/no-var-requires
  const mapTexture = await Pixi.Assets.load(require('@/assets/images/map.png'));
  const mapImg = viewport.addChild(new Pixi.Sprite(mapTexture));
  mapImg.width = 4766;
  mapImg.height = 3370;
  mapImg.anchor.set(0.5);
  mapImg.x = offsetX;
  mapImg.y = offsetY;

  const border = viewport.addChild(new Pixi.Graphics());
  border.moveTo(0, 0);
  border.lineTo(WORLD_WIDTH, 0);
  border.lineTo(WORLD_WIDTH, WORLD_HEIGHT);
  border.lineTo(0, WORLD_HEIGHT);
  border.lineTo(0, 0);
  border.stroke({ width: 6, color: 0x0 });

  const mapContainer = viewport.addChild(new Pixi.Container());

  // eslint-disable-next-line new-cap, global-require, @typescript-eslint/no-var-requires
  const data = require('@/assets/data/mapData.json');
  for (let i = 0; i < Object.keys(data).length; i++) {
    const key = Object.keys(data)[i];
    const container = mapContainer.addChild(new Pixi.Container());

    const subparts = data[key];
    for (let k = 0; k < subparts.length; k++) {
      const subContainer = container.addChild(new Pixi.Container());
      if (subparts[k].path) {
        const path = subContainer.addChild(new Pixi.Graphics());
        const pathData = subparts[k].path;
        for (let j = 0; j < pathData.length; j++) {
          const { type, x, y } = pathData[j];
          if (type === 'move') {
            path.moveTo(x, y);
          } else if (type === 'line') {
            path.lineTo(x, y);
          } else if (type === 'dashed') {
            drawDashedLine(path, pathData[j - 1].x, pathData[j - 1].y, x, y, pathData[j].draw ?? true, pathData[j].dash_array);
          } else if (type === 'stroke') {
            path.stroke({ width: pathData[j].width, color: pathData[j].color });
          } else if (type === 'fill') {
            path.fill(pathData[j].color);
          } else if (type === 'rect') {
            path.rect(x, y, pathData[j].width, pathData[j].height);
          }
        }
      }

      if (subparts[k].label) {
        const label = subContainer.addChild(new Pixi.Text({
          text: subparts[k].label.text ?? subparts[k].name ?? '',
          style: {
            fontSize: subparts[k].label.size,
            fill: subparts[k].label.color ?? 0x0
          }
        }));
        label.resolution = 10;
        label.anchor.set(0.5);
        label.x = subparts[k].label.x;
        label.y = subparts[k].label.y;
      }
    }
  }
}

function createUI(app) {
  // const btn = new Pixi.Container();
  // eslint-disable-next-line new-cap, global-require, @typescript-eslint/no-var-requires
  // const icon = new Pixi.Sprite(Pixi.Texture.from(require('@/assets/images/magnifying-glass.png')));
  // icon.anchor.set(0.5);
  // icon.x = 50;
  // icon.y = 50;
  // const box = new Pixi.Graphics();
  // box.setStrokeStyle(4, 0x0, 0.3);
  // box.roundRect(0, 0, 23, 23, 4);
  // box.fill(0x3e494b);
  // btn.addChild(box);
  // btn.addChild(icon);
  // app.stage.addChild(btn);
  // app.stage.addChild(icon);

  // const font = new Pixi.TextStyle({
  //   fontSize: 32
  // });

  // const input = new PixiInputField('Press To Edit', font);
  // app.stage.addChild(input);
}

onMounted(async () => {
  const canvas = document.getElementById('pixi');
  const resolution = Math.min(window.devicePixelRatio, 2);

  const app = new Pixi.Application();
  await app.init({
    width: getMapSize().width,
    height: getMapSize().height,
    resolution: resolution || 1,
    antialias: resolution <= 1,
    transparent: true,
    canvas,
    background: '#FFFFFF'
  });

  const viewport = new Viewport({
    screenWidth: getMapSize().width,
    screenHeight: getMapSize().height,
    worldWidth: WORLD_WIDTH,
    worldHeight: WORLD_HEIGHT,
    passiveWheel: false,
    events: app.renderer.events
  });

  app.stage.addChild(viewport);

  viewport.drag().pinch().wheel().decelerate();
  viewport.fit();
  viewport.moveCenter(WORLD_WIDTH / 2, WORLD_HEIGHT / 2);
  // viewport.moveCenter(2534, 2085);
  // viewport.zoom(1100, true);
  // TODO
  createMap(viewport);
  // createUI(app);

  window.addEventListener('resize', () => {
    app.canvas.width = getMapSize().width;
    app.canvas.height = getMapSize().height;
    viewport.screenWidth = getMapSize().width;
    viewport.screenHeight = getMapSize().height;
  });

  viewport.on('clicked', (e) => {
    // eslint-disable-next-line no-alert
    alert(JSON.stringify({
      screen: {
        x: e.screen.x,
        y: e.screen.y
      },
      world: {
        x: e.world.x,
        y: e.world.y
      }
    }, null, 2));
  });
});
</script>

